import React, { useState, useEffect } from "react";
import {
  Row,
  Col,
  Tabs,
  Table,
  Tooltip,
  Modal,
  Tag,
  Button,
  Icon,
  message
} from "antd";
import { injectIntl } from "react-intl";

import { ButtonWrapper, BadgeWrapper, Span, ButtonAction } from "./../Styled";
import SuccessCmp from "./../SuccessCmp";
import ErrorCmp from "./../ErrorCmp";
import functions from "./../../utils/functions";

import { getProcess, processFileAfterQuoteFile } from "./../../services";

const TabPane = Tabs.TabPane;

const ProcessList = props => {
  const {
    changeView,
    intl: { messages }
  } = props;

  const [proceses, setProceses] = useState([]);
  const [loading, setLoading] = useState(false);
  const [quote, setQuote] = useState(null);
  const [payResponse, setPayResponse] = useState(null);
  const [loadingPay, setLoadingPay] = useState(false);
  const [visible, setVisible] = useState(false);
  const [processSelected, setProcessSelected] = useState(null);

  useEffect(() => {
    fetchProceses();
    console.log("fetchProceses executed");
    const polling = setInterval(() => {
      fetchProceses(true);
      console.log("Polling fetchProceses executed");
    }, 5000);

    return () => {
      clearInterval(polling);
    };
  }, []);

  const fetchProceses = async polling => {
    try {
      if (!polling) setLoading(true);
      const response = await getProcess();
      setProceses(response.data.process);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      message.error(error.message);
    }
  };

  const pay = async (fileId, quote) => {
    setLoadingPay(true);
    setQuote(quote);
    try {
      await processFileAfterQuoteFile({ fileId, quote });
      let aux = proceses.map(item => {
        if (item.fileId === fileId) {
          item.quoteSelected = quote;
        }
        return item;
      });
      setProceses(aux);
      setLoadingPay(false);
      setPayResponse("success");
    } catch (err) {
      setLoadingPay(false);
      setPayResponse("error");
    }
  };

  const showModal = processSelected => {
    setVisible(true);
    setProcessSelected(processSelected);
  };

  const hideModal = () => {
    setVisible(false);
    setPayResponse(null);
  };

  const download = record => {
    var base64 = record.fileDownload.file;
    var type = record.fileDownload.fileType;
    var name = record.fileDownload.filename;
    window
      .FileDownload(base64, type, name)
      .then(() => console.log("download successful."))
      .catch(err => console.error("download error: ", err));
  };

  const columns = [
    {
      title: messages.process.fileName,
      dataIndex: "fileName",
      key: "fileName",
      sorter: (a, b) => a.fileName - b.fileName
    },
    {
      title: messages.process.processName,
      dataIndex: "processName",
      key: "processName",
      render: (text, record) => (
        <span style={{ textTransform: "capitalize" }}>
          {record.processName}
        </span>
      )
    },
    {
      title: messages.process.language,
      key: "language",
      render: (text, record) => (
        <span style={{ textTransform: "capitalize" }}>
          <Tag style={{ cursor: "default" }} color="#108ee9">
            {record.engineSource}
          </Tag>{" "}
          <Icon type="arrow-right" />{" "}
          <Tag style={{ cursor: "default" }} color="#ff3f1b">
            {record.engineTarget}
          </Tag>
        </span>
      )
    },
    {
      title: messages.process.status,
      dataIndex: "status",
      key: "status",
      render: (text, record) => {
        console.log("record", record);
        if (!(record.quoteSelected && record.quoteSelected.price)) {
          return <Span>{functions.getStatusByText(record.status)}</Span>;
        } else {
          return <Span>{messages.process.paidProcess}</Span>;
        }
      },
      filters: [
        {
          text: "Waiting",
          value: "waiting"
        },
        {
          text: "Waiting Quoting",
          value: "waiting_quoting"
        },
        {
          text: "Quotes Ready",
          value: "quotes_ready"
        },
        {
          text: "Pending Accept/Reject",
          value: "pending_accept_reject"
        },
        {
          text: "Preprocessing",
          value: "preprocessing"
        },
        {
          text: "Processing",
          value: "processing"
        },
        {
          text: "PostProcessing",
          value: "postProcessing"
        }
      ],
      onFilter: (value, record) => record.status === value
    },
    {
      title: messages.process.quote,
      dataIndex: "quoteSelected",
      key: "quotes",
      render: (text, record) => {
        return (
          <span>
            {record.quoteSelected ? (
              <div>
                <Icon type="euro" /> &nbsp;
                {record.quoteSelected.price}
              </div>
            ) : (
              "---"
            )}
          </span>
        );
      }
    },
    {
      title: messages.default.actions,
      key: "action",
      render: (text, record) => (
        <span>
          <Tooltip placement="top" title="See Quotes">
            <ButtonAction
              onClick={() => {
                showModal(record);
              }}
              disabled={record.status !== "quote_ready" || record.quoteSelected}
              size="small"
              type="primary"
              shape="circle"
              icon="wallet"
            />
          </Tooltip>
        </span>
      )
    }
  ];
  const columns1 = [
    {
      title: messages.process.fileName,
      dataIndex: "fileName",
      key: "fileName",
      sorter: (a, b) => a.fileName - b.fileName
    },
    {
      title: messages.process.processName,
      dataIndex: "processName",
      key: "processName",
      render: (text, record) => (
        <span style={{ textTransform: "capitalize" }}>
          {record.processName}
        </span>
      )
    },
    {
      title: messages.process.language,
      key: "language",
      render: (text, record) => (
        <span style={{ textTransform: "capitalize" }}>
          <Tag color="#108ee9">{record.engineSource}</Tag>{" "}
          <Icon type="arrow-right" />{" "}
          <Tag color="#ff3f1b">{record.engineTarget}</Tag>
        </span>
      )
    },
    {
      title: messages.process.status,
      dataIndex: "status",
      key: "status",
      render: status => <Span>{functions.getStatusByText(status)}</Span>
    },
    {
      title: messages.process.quote,
      dataIndex: "quoteSelected",
      key: "quotes",
      render: (text, record) => {
        return (
          <span>
            {record.quoteSelected ? (
              <div>
                <Icon type="euro" /> &nbsp;
                {record.quoteSelected.price}
              </div>
            ) : (
              "---"
            )}
          </span>
        );
      }
    },
    {
      title: messages.default.actions,
      key: "action",
      render: (text, record) => (
        <span>
          <Tooltip placement="top" title="Download">
            <ButtonAction
              onClick={() => {
                download(record);
              }}
              disabled={record.status <= 100}
              size="small"
              type="primary"
              shape="circle"
              icon="download"
            />
          </Tooltip>
        </span>
      )
    }
  ];

  let finished = [],
    inProcess = [];

  if (proceses && proceses.length) {
    finished = proceses.filter(item => {
      return item.status === "downloaded";
    });

    inProcess = proceses.filter(item => {
      return item.status !== "downloaded";
    });
  }

  const operations = (
    <ButtonWrapper
      type="primary"
      icon="file-add"
      onClick={() => {
        changeView("process");
      }}
    >
      New Process
    </ButtonWrapper>
  );

  return (
    <div>
      <Row>
        <Col>
          <Tabs defaultActiveKey="1" tabBarExtraContent={operations}>
            <TabPane
              tab={
                <BadgeWrapper count={inProcess.length}>
                  <span>{messages.process.inProcess}</span>
                </BadgeWrapper>
              }
              key="1"
            >
              <Table
                key="table1"
                size="small"
                loading={loading}
                columns={columns}
                dataSource={inProcess}
              />
            </TabPane>
            <TabPane
              tab={
                <BadgeWrapper count={finished.length}>
                  <span>{messages.process.finished}</span>
                </BadgeWrapper>
              }
              key="2"
            >
              <Table
                key="table2"
                size="small"
                loading={loading}
                columns={columns1}
                dataSource={finished}
              />
            </TabPane>
          </Tabs>
        </Col>
      </Row>

      <Modal
        title="Quotes"
        width={600}
        visible={visible}
        onCancel={hideModal}
        footer={null}
      >
        {payResponse !== "success" && payResponse !== "error" ? (
          <Row justify="center" type="flex">
            {processSelected && processSelected.quotes
              ? processSelected.quotes.map(item => {
                  return (
                    <Col key={item.optionid} xs={24} md={8}>
                      <Row justify="center" type="flex" align="middle">
                        <Col style={{ textAlign: "center", minHeight: 45 }}>
                          <h4 style={{ minHeight: 41 }}>{item.option}</h4>

                          <Button
                            loading={
                              loadingPay && quote.optionid === item.optionid
                            }
                            onClick={() => {
                              pay(processSelected.fileId, item);
                            }}
                            style={{ marginTop: 16 }}
                            type="primary"
                          >
                            {!(
                              loadingPay && quote.optionid === item.optionid
                            ) && (
                              <React.Fragment>
                                <Icon type="euro" /> {item.price}
                              </React.Fragment>
                            )}
                          </Button>
                        </Col>
                      </Row>
                    </Col>
                  );
                })
              : null}
          </Row>
        ) : null}
        {payResponse === "success" ? <SuccessCmp messages={messages} /> : null}
        {payResponse === "error" ? <ErrorCmp messages={messages} /> : null}
      </Modal>
    </div>
  );
};

export default injectIntl(ProcessList);
